import React, { Component } from 'react';
import { ReactiveBase, DataSearch } from '@appbaseio/reactivesearch';
import './App.css';
import './components/Variables'
import theme from './theme';
import Header from './components/Header';
import Results from './components/Results';
import {ES_IP, ES_INDEX, ES_AUTH} from './components/Variables';

class App extends Component {

	constructor(props) {
		super(props);
		this.state = {
			currentTopics: [],
		};
	}

	/* setTopics will set topics passed to it -> pass to header */
	setTopics = (currentTopics) => {
		this.setState({
			currentTopics: currentTopics || [],
		});
	}

	/* toggleTopic will remove a topic from state if already present and add if not present */
	toggleTopic = (topic) => {
		const { currentTopics } = this.state;
		const nextState = currentTopics.includes(topic)
			? currentTopics.filter(item => item !== topic)
			: currentTopics.concat(topic);
		this.setState({
			currentTopics: nextState,
		});
	}

	render() {
		return (
			<section className="container">
				<ReactiveBase
					app={ES_INDEX}
					url={ES_IP}
					credentials={ES_AUTH}
					theme={theme}
				>
					{/* DataSearch components*/}
					<div className="flex row-reverse app-container">
						<Header />
						<div className="results-container">
							<DataSearch
								componentId="full_searchbar"
								filterLabel="Search"
								/* Define the data fields that the search engine will look for in the Elasticsearch index*/
								dataField={['content', 'author', 'doc_title', 'web_title']}
								fieldWeights={[2, 1, 1, 2]}
								placeholder="Search reports ..."
								autosuggest={false}
								iconPosition="left"
								URLParams={true}
								highlight={true}
								customHighlight={(props) => ({
									highlight: {
										pre_tags: ['<mark>'],
										post_tags: ['</mark>'],
										fields: {
											"content": {},
											"web_title": {},
											"doc_title": {},
										},
										fragment_size: 300,
										number_of_fragments: 1,
									},
								})}
								queryFormat="or"
								fuzziness={1}
								debounce={1000}
								className="data-search-container results-container"
								innerClass={{
									input: 'search-input',
								}}
								showClear={true}
							/>

							<Results currentTopics={this.state.currentTopics} toggleTopic={this.toggleTopic} />
							
						</div>
					</div>


				</ReactiveBase>
			</section>
		);
	}
}

export default App;
